import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router' //vue-Router
import router from "./router/router.js"

// axios
import Axios from 'axios'
// qs
// import qs from 'qs'
// 
import {
	post,
	get
} from './utils/http.js'

Vue.prototype.$axios = Axios
Vue.prototype.$post = post
Vue.prototype.$get = get
//mandmobile
import "mand-mobile/components/_style/global.styl";
import "normalize.css";

//index
import './assets/indexicon/iconfont.css';
// home
import './assets/home_icon/iconfont.css';
// assets
import "./assets/assets_icon/iconfont.css"
// vant
import 'vant/lib/index.css';
import {
	Tabbar,
	TabbarItem,
	Icon
} from 'vant';
Vue.use(Tabbar);
Vue.use(TabbarItem);
Vue.use(Icon);

Vue.use(VueRouter); //  vue-ROuter

Vue.config.productionTip = false

new Vue({
	render: h => h(App),
	router, //
}).$mount('#app')
