<template>
	<div id="add_bank_card">
		<div id="add_bank_card_head">
			<van-nav-bar :border="false" title="" left-text="" left-arrow @click-left="onClickLeft">
				<template #right>
					<van-icon name="question-o" size="0.5rem" />
				</template>
			</van-nav-bar>
		</div>
		<div id="Tips1"></div>
		<div id="add_bank_card_from">
			<van-cell-group>
				<van-field label="" right-icon="info" @click-right-icon="onClickRight(1)" value="" readonly />
				<van-field v-model="digitVal" type="digit" autofocus="true" label="" placeholder="/" :clearable="true"
				 :clear="checkCard()" />
			</van-cell-group>
		</div>
		<div id="Tips1">,<span style="color: #108ee9;" @click="onClickRight(2)"></span></div>
		<div id="submit">
			<van-button @click="nextStep()" type="primary" size="large" :disabled="val"></van-button>
		</div>
		<div>
			<van-popup v-model="show1" :close-on-click-overlay="false">
				<span></span>
				<div style="line-height: 0.6rem;"></div>
				<p @click="onClickRight(0)"></p>
			</van-popup>
		</div>
		<div class="security">
			<van-popup v-model="show2" :close-on-click-overlay="false">
				<span></span>
				<div style="line-height: 0.6rem;">1.<br>2.<br>3.<br>4.<br>5.,99999</div>
				<p @click="onClickRight(0)"></p>
			</van-popup>
		</div>
	</div>
</template>

<script>
	import bankInfo from '../../utils/banks.js'
	import {
		NavBar,
		Toast,
		Icon,
		Field,
		Button,
		Popup
	} from 'vant';
	export default {
		components: {
			[NavBar.name]: NavBar,
			[Toast.name]: Toast,
			[Icon.name]: Icon,
			[Field.name]: Field,
			[Button.name]: Button,
			[Popup.name]: Popup
		},
		data() {
			return {
				digitVal: '',
				show1: false,
				show2: false,
				val: true,
				bankList: [],
				cardNo: ''
			}
		},
		methods: {
			onClickLeft() {
				this.$router.go(-1);
			},
			onClickRight(num) {
				if (num == 1) {
					this.show1 = true;
				} else if (num == 2) {
					this.show2 = true;
				} else {
					this.show1 = false;
					this.show2 = false;
				}
			},
			async nextStep() {
				// 
				var val = this.digitVal;

				let arrAll = JSON.parse(localStorage.getItem("bankList"));
				// console.log(arrAll);
				if (arrAll) {
					// 
					for (var i = 0; i < arrAll.length; i++) {
						if (arrAll[i].cardNo == val) {
							Toast.fail('');
							return;
						}
					}
				}

				// 
				var result = await this.$get(
					'https://ccdcapi.alipay.com/validateAndCacheCardInfo.json?_input_charset=utf-8&cardNo=' + val +
					'&cardBinCheck=true'
				).then(function(res) {
					return res;
				})
				// console.log(result);
				//6217996100080404583
				// 6228480831781064918
				// 622908553020636611
				// 
				if (result.validated == true) {

					// DC: "",
					//    CC: "",
					//    SCC: "",
					//    PC: ""
					// 
					if (result.cardType == 'DC') {
						result.cardType = '';
					}
					if (result.cardType == 'CC') {
						result.cardType = '';
					}
					if (result.cardType == 'SCC') {
						result.cardType = '';
					}
					if (result.cardType == 'PC') {
						result.cardType = '';
					}

					var carkType = bankInfo.bankList[result.bank] + result.cardType;
					// 
					localStorage.removeItem('cardNum');
					localStorage.setItem('cardNum', val);
					this.$router.push({
						path: '/phone_number_check',
						query: {
							carkType: carkType,
						}
					})


				} else {
					Toast.fail('');
				}
			},
			checkCard() {
				var but = document.querySelector(".van-button__content");
				var b = document.getElementsByTagName("button")[0];
				if (but != null && b != undefined) {
					if (this.digitVal != "") {
						but.style.background = "#108ee9";
						but.style.color = "#FFFFFF";
						this.val = false;
					} else {
						but.style.background = "#FFFFFF";
						but.style.color = "#999999";
						this.val = true;
					}
				}
			}
		}

	}
</script>

<style>
	@import '../../assets/css/add_bank_card.css';
</style>
