<template>
	<div id="ph_ch_co">
		<div id="ph_ch_co_head">
			<van-nav-bar :border="false" left-text="" left-arrow @click-left="onClickLeft" />
		</div>
		<div id="ph_ch_co_content">
			<div class="md-example-child md-example-child-captcha-1" @keydown="headCode()">
			    <md-captcha
			      :maxlength="4"
			      :isView="true"
				  @submit="headCode"
			      brief=""
			    >
			      {{phoneNum}}
			    </md-captcha>
			  </div>
		</div>	
	</div>
</template>

<script>
	import bankInfo from '../../utils/banks.js';
	import {
		NavBar,
		Toast
	} from 'vant';
	import {
		Captcha
	} from 'mand-mobile';
	export default {
		name: 'captcha-demo',
		components: {
			[NavBar.name]: NavBar,
			[Toast.name]: Toast,
			[Captcha.name]: Captcha,
		},
		data() {
			return {
				digitVal: '',
				show: false,
				phoneNum: this.$route.query.phoneNum,
				right_icon: "info",
				val: true,
				bankList: [],
				cardNo: '',
			}
		},
		methods: {
			onClickLeft() {
				this.$router.go(-1);
			},
			headCode(val){
				// console.log(val);
				this.nextStep();
			},
			async nextStep() {
				// 
				var val=localStorage.getItem('cardNum');
				// console.log(val);
				// 
				var result = await this.$get(
					'https://ccdcapi.alipay.com/validateAndCacheCardInfo.json?_input_charset=utf-8&cardNo=' + val +
					'&cardBinCheck=true'
				).then(function(res) {
					return res;
				})

				// 6217996100080404583
				// 6228480831781064918
				// 622908553020636611
				// 6236683480001168113 
				// 
				if (result.validated == true) {

					// DC: "",
					// CC: "",
					// SCC: "",
					// PC: ""
					// 
					if (result.cardType == 'DC') {
						result.cardType = '';
					}
					if (result.cardType == 'CC') {
						result.cardType = '';
					}
					if (result.cardType == 'SCC') {
						result.cardType = '';
					}
					if (result.cardType == 'PC') {
						result.cardType = '';
					}


					//    

					var bankName = bankInfo.bankList[result.bank];

					//
					let arrAll = [];
					
					// 
					let obj = {
						cardType: result.cardType,
						cardNo: result.key,
						bankName: bankName,
						Icon: result.bank
					};
					// 
					if (result.key == "") {
						obj.color = "#c13d4b";
					} else if (bankName == "") {
						obj.color = "#3b8783";
					} else if (bankName == "") {
						obj.color = "#37827d";
					} else if (bankName == "") {
						obj.color = "#486ea7";
					} else if (bankName == "") {
						obj.color = "#224588";
					} else {
						obj.color = "#3d8a86";
					}
					
					// console.log(obj);
					
					if (localStorage.getItem("bankList")!=null) {
						// console.log(111);
						arrAll = JSON.parse(localStorage.getItem("bankList"));
						// console.log(arrAll);
					} else {
						// console.log(222)
						arrAll = [];
					}
					// 
					arrAll.push(obj);
					// console.log(arrAll);

					// 
					arrAll = JSON.stringify(arrAll); //JSON
					localStorage.setItem("bankList", arrAll);
					
					Toast.setDefaultOptions({
						duration: 1000
					});	
					Toast.success('');
					setTimeout(() => {
						this.$router.push('/bank_card');
					}, 1000);


				} else {
					Toast.fail('');
				}
			}
		}
	}
</script>
<style lang="stylus">
.md-example-child-captcha-1
  height 650px
  padding 30px 0
  .md-number-keyboard
    margin-top 30px
</style>
<style>
	#ph_ch_co {
		position: fixed;
		width: 100%;
		height: 100%;
		top: 0px;
		background-color: #f5f5f9;
	}

	#ph_ch_co #ph_ch_co_head .van-nav-bar {
		background-color: #FFFFFF;
		height: 1rem;
	}

	#ph_ch_co #ph_ch_co_head .van-nav-bar__text {
		font-size: 0.35rem;
		color: #333333;
	}

	#ph_ch_co #ph_ch_co_head .van-nav-bar .van-icon-arrow-left::before {
		font-size: 0.5rem;
		color: #1989fa;
	}
	
	#ph_ch_co_content{

	}
	#ph_ch_co #ph_ch_co_content .md-captcha-footer{
		padding: 0 0.2rem;
		font-size: 0.3rem;
		height: 1rem;
	}
	#ph_ch_co #ph_ch_co_content .md-captcha-btn{
		font-size: 0.35rem;
	}
	
	#ph_ch_co #ph_ch_co_content .md-captcha-content{
		font-size: 0.35rem;
	}
</style>
